export default {
  layout:"OnlyNav",
  data() {
    return {
      playerName: ""
    };
  },
  methods: {
    searchPlayer() {
      let cleanPlayerName = this.playerName.trim();

      if (cleanPlayerName !== "") {
        let encodedPlayerName = encodeURIComponent(cleanPlayerName);
        this.$router.replace(`/mystats/${encodedPlayerName}`);
      }
    }
  }
};
